
html {
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
html::-webkit-scrollbar {
  width: 0;
}
body, div, span, header, footer, nav, section, aside, article, ul, li, a, p, textarea, button, input, select {
  padding: 0;
  margin: 0;
  list-style: none;
  font-weight: normal;
  font-style: normal;
  text-decoration: none;
  border: none;
  font-family: 'Hiragino Sans GB', 'Hiragino Sans GB W3', 'Microsoft YaHei', 'WenQuanYi Micro Hei', sans-serif;
  -webkit-tap-highlight-color: transparent;
}
body:focus, div:focus, span:focus, header:focus, footer:focus, nav:focus, section:focus, aside:focus, article:focus, ul:focus, li:focus, a:focus, p:focus, textarea:focus, button:focus, input:focus, select:focus {
    outline: none;
}
html {
  -webkit-overflow-scrolling: touch !important;
  overflow: auto !important;
  height: 100% !important;
}
body {
  -webkit-overflow-scrolling: touch !important;
  overflow: auto !important;
  position: relative;
  height: 100% !important;
  width: 100%;
  background: #f9f9f9;
  font-size: 12px;
}
.clearfix {
  zoom: 1;
}
.clearfix::after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
}
.vertical-middle {
  height: 100%;
  width: 0;
  display: inline-block;
  vertical-align: middle;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.mt6 {
  margin-top: 6px;
}
.ml60 {
  margin-left: 60px;
}
.pointer {
  cursor: pointer;
}
[v-cloak] {
  display: none;
}
.slide-fade-enter-active {
  transition: all .6s ease;
}
.slide-fade-leave-active {
  transition: all .5s ease;
}
.slide-fade-enter,
.slide-fade-leave-active {
  transform: translateX(60px);
  opacity: 0;
}
body, div, span, header, footer, nav, section, aside, article, ul, li, a, p, h1, h2, h3, h4, h5, i, b, textarea, button, input, select {
  box-sizing: border-box;
}
.scroll-wrapper {
  position: relative;
  overflow: hidden;
  -ms-touch-action: none;
      touch-action: none;
}
@keyframes refreshing-loader {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
.refreshing {
  position: relative;
  z-index: 9;
  width: 50px;
  height: 50px;
  background-color: #ffffff;
  margin: -62px auto 0;
  border: 1px solid #f0f0f0;
  box-shadow: 0 0 13px 3px #f0f0f0;
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.scroll {
  position: relative;
  top: 12px;
  z-index: 3;
  -ms-touch-action: none;
      touch-action: none;
}
.loading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px;
}
.loading .data-loader {
    margin-right: 4px;
}
.data-loader, .refreshing-loader {
  animation: refreshing-loader 1000ms infinite linear;
  border-radius: 2.4em;
  border: 0.3em solid #9ac;
  border-left-color: transparent;
  color: transparent;
  display: inline-block;
  font-size: 10px;
  line-height: 1.2;
  width: 2em;
  height: 2em;
  text-indent: 100%;
}
.refreshing-loader {
  border: 0.4em solid #9ac;
  width: 3em;
  height: 3em;
}
.refreshing-loader:after {
    display: block;
    border: 0.5em solid transparent;
    border-top-color: #9ac;
    border-left-color: #9ac;
    content: '';
    width: 0;
    height: 0;
    overflow: hidden;
    margin-left: -0.2em;
    margin-top: 0.39em;
}
.refreshing-loader.will {
    border-color: #f0f0f0;
    border-left-color: transparent;
}
.refreshing-loader.will:after {
      border-top-color: #f0f0f0;
      border-left-color: #f0f0f0;
}
.refreshing-loader.doing {
    border-color: #979797;
    border-left-color: transparent;
}
.refreshing-loader.doing:after {
      border-top-color: #979797;
      border-left-color: #979797;
}
.refreshing-loader.did {
    border-color: #6EB8F1;
    border-left-color: transparent;
}
.refreshing-loader.did:after {
      border-top-color: #6EB8F1;
      border-left-color: #6EB8F1;
}
.refreshing-loader.error {
    border-color: #E44F51;
    border-left-color: transparent;
}
.refreshing-loader.error:after {
      border-top-color: #E44F51;
      border-left-color: #E44F51;
}



